import React, { useEffect, useState } from 'react';
import {StyledSection, StyledForm, StyledButton} from './PopupStyle';

interface ISignup {
    signup: boolean
    setConfirmSignup: (value: boolean | ((prevHeight: boolean) => boolean)) => void,
    setSignup: (value: boolean | ((prevHeight: boolean) => boolean)) => void,
}

const Popup: React.FC<ISignup> = ({signup, setSignup, setConfirmSignup}) => {

    const [name, setName] = useState<string>('');
    const [phone, setPhone] = useState<string>('');
    const [age, setAge] = useState<string>('');
    const [date, setDate] = useState<string>('');
    const [disabled, setDisabled] = useState<boolean>(true);

    useEffect(() => {
        if(name && phone && age && date) {
            setDisabled(false);
        } else setDisabled(true);
    }, [name, phone, age, date]);

    function handleSubmit(event: React.SyntheticEvent) {
        event.preventDefault();
        if(name && phone && age && date) {
            setConfirmSignup(prev => !prev);
            setSignup(prev => !prev);
        }
        setName('');
        setPhone('');
        setAge('');
        setDate('');
    };

    function handleClickClose(): void {
        setSignup(prev => !prev);
    };
    
    return (
        <StyledSection signup={signup}>
            <StyledForm onClick={event => event.stopPropagation()} onSubmit={handleSubmit} disabled={disabled}>
                <h3>–ó–∞–ø–∏—Å—å –Ω–∞ —Ä–∞–∑–æ–≤–æ–µ –∑–∞–Ω—è—Ç–∏–µ</h3>
                <h5>–ü—Ä–æ—Ö–æ–¥—è—Ç –ø–æ –≤—Ç–æ—Ä–Ω–∏–∫–∞–º –∏ —á–µ—Ç–≤–µ—Ä–≥–∞–º —Å 16:00 –¥–æ 17:00</h5>
                <label htmlFor="name"></label>
                    <input type="text" id='name' value={name} onChange={event=>setName(event.target.value)} placeholder='–í–∞—à–µ –∏–º—è'/>
                <label htmlFor="phone"></label>
                    <input type="tel" id='phone' value={phone} onChange={event=>setPhone(event.target.value)} placeholder='–¢–µ–ª–µ—Ñ–æ–Ω'/>
                <label htmlFor="age"></label>
                    <input type='text' id='age' value={age} onChange={event=>setAge(event.target.value)} placeholder='–í–æ–∑—Ä–∞—Å—Ç —Ä–µ–±–µ–Ω–∫–∞'/>
                <label htmlFor="date"></label>
                    <input type="date" id='date' value={date} onChange={event=>setDate(event.target.value)} placeholder='–î–∞—Ç–∞ –∑–∞–Ω—è—Ç–∏—è'/>
                <input type="submit"/>
                <StyledButton onClick={handleClickClose}><img src="./img/close.png" alt=""/></StyledButton>
            </StyledForm>
        </StyledSection>
    );
};

export default Popup;